import { createSlice } from "@reduxjs/toolkit";

const boardsSlice = createSlice({
  name: "boardsSlice",
  initialState: {
    boards: [
      {
        id: 0,
        title: "",
        description: "  ",
        
      },
      {
        id: 1,
        title: "",
        description: "  ",
      },
      {
        id: 2,
        title: "",
        description: "  ",
      },
      {
        id: 3,
        title: "",
        description: "  ",
      },
      {
        id: 4,
        title: "",
        description: "  ",
      },
      {
        id: 5,
        title: "",
        description: "  ",
      },
      {
        id: 6,
        title: "",
        description: "  ",
      },
    ],
  },
  reducers: {
    boardsRedux: (state, action) => {
      state.boards = action.payload;
    },
  },
});
export default boardsSlice;
export const { boardsRedux } = boardsSlice.actions;
